import {
  AfterViewInit,
  Component,
  ElementRef,
  EventEmitter,
  HostBinding,
  Input,
  NgZone,
  OnDestroy,
  Output,
  TemplateRef,
  ViewChild
} from '@angular/core';
import {ENTER, ESCAPE} from '@angular/cdk/keycodes';
import {animate, state, style, transition, trigger, useAnimation} from '@angular/animations';
import {take, takeUntil} from 'rxjs/operators';
import {ReplaySubject} from 'rxjs';
import {clone} from 'lodash-es';

import {getEventKeyCode} from '../../common/key-codes';
import {ANIMATION_DURATION, ANIMATION_NAME, ANIMATION_TIMING_FUNCTION} from '../../common/animation';

import {
  RxListBuilderItem,
  RxListBuilderSelectionMode,
} from '../rx-list-builder/index';
import {RxSelectOption} from '../rx-select/index';
import {AdaptCustomAction, AdaptCustomActionsAlignEnum} from '../../custom-actions/index';

import {RxListItemTexts} from './adapt-rx-list-item.types';

let nextUniqueId: number = 0;

@Component({
  selector: 'adapt-rx-list-item',
  templateUrl: './adapt-rx-list-item.component.html',
  animations: [
    trigger('editErrorAnimation', [
      transition('void => *', [
        useAnimation(ANIMATION_NAME.height)
      ]),
      transition('* => void', [
        useAnimation(ANIMATION_NAME.height, {
          params: {
            from: '*',
            to: '0'
          }
        })
      ])
    ]),
    trigger('expandedAnimation', [
      state('0', style({transform: 'rotate(0deg)'})),
      state('1', style({transform: 'rotate(90deg)'})),
      transition('0 => 1', [
        useAnimation(ANIMATION_NAME.rotate, {
          params: {
            from: '0',
            to: '90'
          }
        })
      ]),
      transition('1 => 0', [
        useAnimation(ANIMATION_NAME.rotate, {
          params: {
            from: '90',
            to: '0'
          }
        })
      ])
    ]),
    trigger('editModeAnimation', [
      state('void', style({opacity: 0})),
      transition('void<=>*', animate(ANIMATION_DURATION.xtraFast))
    ]),
    trigger('hideAnimation', [
      state('1', style({
        opacity: 1,
        zIndex: '*',
      })),
      state('0', style({
        opacity: 0,
        zIndex: -1,
      })),
      transition('0 => 1', [
        style({opacity: 0, zIndex: -1}),
        animate(`${ANIMATION_DURATION.xtraFast} ${ANIMATION_TIMING_FUNCTION.base.transitionIn}`,
          style({opacity: 1, zIndex: '*'})
        )
      ]),
      transition('1 => 0', [
        style({opacity: 1, zIndex: 0}),
        animate(`${ANIMATION_DURATION.xtraFast} ${ANIMATION_TIMING_FUNCTION.base.transitionOut}`,
          style({opacity: 0, zIndex: -1})
        )
      ])
    ])
  ]
})
export class AdaptRxListItemComponent implements AfterViewInit, OnDestroy {
  editMode: boolean = false;
  inputControlValue: string;
  customActionsAlignedTo: AdaptCustomActionsAlignEnum = AdaptCustomActionsAlignEnum.Right;

  /**
   * List item
   * @docs-default
   */
  @Input() item: RxListBuilderItem | RxSelectOption;

  /**
   * Highlight search query on item name
   * @docs-default
   */
  @Input() searchQuery: string;
  @Input() texts: RxListItemTexts;

  /**
   * Whether edited item could be saved, based on provided itemValidation function
   * @docs-default
   */
  @Input() allowSaving: boolean;
  /**
   * When present, sets the maximum label length that can be saved when edited.
   * @docs-default
   */
  @Input() maxLength: number;
  @Input() editError: string;
  @Input() hideControls: boolean = false;
  @Input() disabled: boolean;
  @Input() readonly: boolean;
  @Input() hideEdit: boolean = false;
  @Input() hideDelete: boolean = false;
  @Input() itemFormatter: (item: RxListBuilderItem | RxSelectOption) => string;
  @Input() itemContentTemplate: TemplateRef<{ $implicit: RxListBuilderItem | RxSelectOption }>;
  @Input() itemSetterProp: string;
  @Input() isExpanded: boolean = true;
  @Input() testID: string;
  @Input() dragDisabled: boolean;

  /**
   * Specifies the selection mode, valid values are "single" and "multiple"
   * @since 12.27.0, 14.5.0
   * @docs-default
   */
  @Input() selectionMode: RxListBuilderSelectionMode;

  /* eslint-disable @angular-eslint/no-output-on-prefix */
  /**
   * Fires on switching to the component edit mode
   */
  @Output() readonly onItemEdit: EventEmitter<RxListBuilderItem> = new EventEmitter<RxListBuilderItem>();
  /* eslint-enable @angular-eslint/no-output-on-prefix */
  // eslint-disable-next-line @angular-eslint/no-output-on-prefix
  @Output() readonly onItemDelete: EventEmitter<RxListBuilderItem> = new EventEmitter<RxListBuilderItem>();
  // eslint-disable-next-line @angular-eslint/no-output-on-prefix
  @Output() readonly onItemUpdate: EventEmitter<[RxListBuilderItem, RxListBuilderItem]> = new EventEmitter<[RxListBuilderItem, RxListBuilderItem]>();
  // eslint-disable-next-line @angular-eslint/no-output-on-prefix
  @Output() readonly onListItemValueChange: EventEmitter<[string, string]> = new EventEmitter<[string, string]>();

  // eslint-disable-next-line @angular-eslint/no-output-on-prefix
  @Output() readonly onKeyUp: EventEmitter<KeyboardEvent> = new EventEmitter();
  // eslint-disable-next-line @angular-eslint/no-output-on-prefix
  @Output() readonly onKeyDown: EventEmitter<KeyboardEvent> = new EventEmitter();

  // eslint-disable-next-line @angular-eslint/no-output-on-prefix
  @Output() readonly onExitEditMode: EventEmitter<null> = new EventEmitter();

  /**
   * Emit when the item selection state changed
   * @since 12.27.0, 14.5.0
   */
  @Output() readonly selectionStateChange: EventEmitter<RxListBuilderItem> = new EventEmitter<RxListBuilderItem>();

  // drag-and-drop
  @Output() readonly dragStart: EventEmitter<{ event: DragEvent; item: AdaptRxListItemComponent }> = new EventEmitter();
  @Output() readonly dragEnter: EventEmitter<{ event: DragEvent; item: AdaptRxListItemComponent }> = new EventEmitter();
  @Output() readonly dragOver: EventEmitter<{ event: DragEvent; item: AdaptRxListItemComponent }> = new EventEmitter();
  @Output() readonly dragLeave: EventEmitter<{ event: DragEvent; item: AdaptRxListItemComponent }> = new EventEmitter();
  // eslint-disable-next-line @angular-eslint/no-output-native
  @Output() readonly drop: EventEmitter<DragEvent> = new EventEmitter();
  @Output() readonly dragEnd: EventEmitter<void> = new EventEmitter();

  @HostBinding('attr.id') @Input() id: string = `rx-list-item-${++nextUniqueId}`;

  @ViewChild('listItemContainer', {static: true}) listItemContainer: ElementRef<HTMLElement>; // Uses for DnD in RxListBuilder

  @ViewChild('listItemDnDSymbol', {static: false}) listItemDnDSymbol: ElementRef<HTMLElement>; // Uses for DnD in RxListBuilder

  private _destroyed$: ReplaySubject<boolean> = new ReplaySubject(1);
  private _dragStartEventHandler: (ev: DragEvent) => void;
  private _dragOverEventHandler: (ev: DragEvent) => void;
  private _dragEnterEventHandler: (ev: DragEvent) => void;
  private _dragLeaveEventHandler: (ev: DragEvent) => void;
  private _dropEventHandler: (ev: DragEvent) => void;
  private _dragEndEventHandler: () => void;

  constructor(public _element: ElementRef,
              private _ngZone: NgZone) {
    this._dragStartEventHandler = this.onDragStart.bind(this);
    this._dragOverEventHandler = this.onDragOver.bind(this);
    this._dragEnterEventHandler = this.onDragEnter.bind(this);
    this._dragLeaveEventHandler = this.onDragLeave.bind(this);
    this._dropEventHandler = this.onDrop.bind(this);
    this._dragEndEventHandler = this.onDragEnd.bind(this);
  }

  ngAfterViewInit(): void {
    const el: HTMLElement = this._element.nativeElement;

    this._ngZone.runOutsideAngular(() => {
      el.addEventListener('dragstart', this._dragStartEventHandler);
      el.addEventListener('dragenter', this._dragEnterEventHandler);
      el.addEventListener('dragover', this._dragOverEventHandler);
      el.addEventListener('dragleave', this._dragLeaveEventHandler);
      el.addEventListener('drop', this._dropEventHandler);
      el.addEventListener('dragend', this._dragEndEventHandler);
    });
  }

  ngOnDestroy(): void {
    this._destroyed$.next(true);
    this._destroyed$.complete();

    const el: HTMLElement = this._element.nativeElement;

    el.removeEventListener('dragstart', this._dragOverEventHandler);
    el.removeEventListener('dragover', this._dragOverEventHandler);
    el.removeEventListener('dragenter', this._dragEnterEventHandler);
    el.removeEventListener('dragleave', this._dragLeaveEventHandler);
    el.removeEventListener('drop', this._dropEventHandler);
    el.removeEventListener('dragend', this._dragEndEventHandler);
  }

  get hasError(): boolean {
    return (this.itemFormatter(this.item) !== this.inputControlValue) && !this.allowSaving && Boolean(this.editError);
  }

  get currentEditActions(): AdaptCustomAction[] {
    return [
      {
        name: this.texts.cancel,
        visible: true,
        onClick: this.exitEditMode.bind(this)
      },
      {
        name: this.texts.save,
        visible: this.allowSaving,
        onClick: this.updateItem.bind(this)
      }
    ];
  }

  onDragStart(event: DragEvent): void {
    this.dragStart.emit({event, item: this});
  }

  onDragEnter(event: DragEvent): void {
    this.dragEnter.emit({event, item: this});
  }

  onDragOver(event: DragEvent): void {
    this.dragOver.emit({event, item: this});
  }

  onDragLeave(event: DragEvent): void {
    this.dragLeave.emit({event, item: this});
  }

  onDrop(event: DragEvent): void {
    this.drop.emit(event);
  }

  onDragEnd(): void {
    this.dragEnd.emit();
  }

  exitEditMode(): void {
    this.editMode = false;
    this.onExitEditMode.emit();
  }

  enterEditMode(): void {
    this.editMode = true;
    this.inputControlValue = this.itemFormatter(this.item);
    this.onItemEdit.emit(this.item);
  }

  deleteItem(): void {
    this.onItemDelete.emit(this.item);
  }

  updateItem(): void {
    const oldValue = clone(this.item);

    if (this.itemSetterProp) {
      this.item[this.itemSetterProp] = this.inputControlValue;
    } else {
      this.item = this.inputControlValue;
    }

    this.onItemUpdate.emit([oldValue, this.item]);
    this.exitEditMode();
  }

  listItemValueChange(): void {
    this.onListItemValueChange.emit([this.itemFormatter(this.item), this.inputControlValue]);
  }

  onInputKeyDown(event: KeyboardEvent): void {
    const keyCode = getEventKeyCode(event);

    if (keyCode === ESCAPE || keyCode === ENTER) {
      event.preventDefault();

      switch (keyCode) {
        case ESCAPE: {
          this.exitEditMode();
          break;
        }
        case ENTER: {
          if (this.allowSaving) {
            this.updateItem();
          }
          break;
        }
      }
    }
  }

  onUp(e: KeyboardEvent): void {
    if (this.disabled || this.readonly) {
      return;
    }

    this._onKeyboardEvent(e);
    this.onKeyUp.emit(e);
  }

  onDown(e: KeyboardEvent): void {
    if (this.disabled || this.readonly) {
      return;
    }

    this._onKeyboardEvent(e);
    this.onKeyDown.emit(e);
  }

  onSpace(e: KeyboardEvent, item: RxListBuilderItem): void {
    if (this.disabled || this.readonly || !this.selectionMode) {
      return;
    }

    this._onKeyboardEvent(e);
    this.handleCheckedChange(item);
  }

  handleCheckedChange(item: RxListBuilderItem): void {
    if (this.disabled || this.readonly || item.disabled) {
      return;
    }

    this.selectionStateChange.emit(this.item);
  }

  private _onKeyboardEvent(e: KeyboardEvent): void {
    e.preventDefault();

    this._ngZone.onStable
      .pipe(takeUntil(this._destroyed$), take(1))
      .subscribe(() => {
        (e.target as HTMLElement).focus();
      });
  }
}
